export default{
	
	data(){
		return{
			theMid:'',
			showNewsData: [
				{
					title:'最后4小时！最后4小时！最后4小时！最后4小时！',
					content:'重复关注会员：昵称为【开心就好】的朋友，在2020-06-15 10:36:03已经关注成为【惠农6:03已经关公',
					time:1593571037
				},
				{
					title:'会员通知',
					content:'618最后冲刺，看直播抽免单、赢万千618最后冲刺，看直播抽免单、赢万千',
					time:1593571037
				},
				{
					title:'订单通知',
					content:'618最后冲刺，看直播抽免单、赢万千618最后冲刺，看直播抽免单、赢万千',
					time:1593571037
				},
				{
					title:'我的评价',
					content:'618最后冲刺，看直播抽免单、赢万千618最后冲刺，看直播抽免单、赢万千',
					time:1593571037
				}
			],
			
			page:1,
			loadingText: '正在加载数据...',
		}
	},
	onReachBottom() {
	    this.page++;
	    this.loadingText='正在加载更多数据...';
	    this.getServicList();
	},
	onPullDownRefresh() {
		this.getServicList();
	},
	onLoad(eee) {
		
		var _this = this;
		uni.getStorage({
			key: 'mid',
			success: function(res) {
				console.log('getStoragegemid' + res.data);
				_this.theMid = res.data;
				// _this.getServicList();
			}
		});
		// this.getServicList();
	},
	methods:{
		
		
		async getServicList(){
			
			var serverUrl="Home/Message/getList";
			var data={
				mid:this.theMid,
				type:this.currentIndex,
				limit:10,
			    page:this.page
			}
			var res = await this.requestFromServer({
				url:serverUrl,
				data:data
			})
			if(this.page==1){
				this.showNewsData=[];
			}
			uni.stopPullDownRefresh();
			if(res.data){
				this.showNewsData=this.showNewsData.concat(res.data);
				if(res.data.length<10){
					this.loadingText='没有更多数据了';
				}else{
					this.loadingText='加载更多数据...';
				}
			}else{
				this.loadingText='没有更多数据了';
			}
			
		},
		detail(){
			uni.navigateTo({
				url:'./myOrderDetail'
			})
		},
		dupOrderNum(ddbh){
		    uni.setClipboardData({
		    	data: ddbh
		    });
			uni.showToast({
				icon:'none',
				title:'订单号已复制'
			})
		},
		
		dateFormat:function(time) {
			return this.getDateSecond(time);
		},
	}
}
